.App {
  text-align: center;
}

h1 {
  color: rgb(236, 22, 236);
}

.Add {
  text-align: center;
  padding-top: 80px;
}

.score {
  color: darkblue;
  font-size: 18px;
  font-style: bold;
}

input {
  height: 35px;
  width: 250px;
  border-radius: 15px;
  font-size: 20px;
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
